.cryo-wrapper {
  background-color: rgba(0, 0, 0, 0.3);
}

.cryo-default-style {
  background-color: rgba(255,255,255,0.7);
  color: #000;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 0 4px #666;
}
.cryo-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cryo-wrapper .fixed {
  position: fixed;
}

.cryo-freeze {
  pointer-events: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
       user-select: none;
}
